  $tabs-tab-hover-bg: $base-element-bg !default;
  $tabs-tab-active-bg: color.change(lighten($tabs-tab-bg, 62.5%), $alpha: 0.2) !default;
  $tabs-tab-active-border-color: transparent !default;
