  $tabs-tab-active-bg: color.change(darken($tabs-tab-bg, 62.5%), $alpha: 0.2) !default;
  $tabs-tab-active-border-color: darken($tabs-tab-bg, 12.16) !default;
  $tabs-tab-hover-border-color-horizontal: $base-border-color !default;
